import { Button } from "@mui/material";
// import React from "react";
import { useForm } from "react-hook-form";
import  './form.css'


export const Form = () => {

    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: 'onBlur',
    });

    const cbSubmit = (data) => {
        console.log(data);
    }

    // console.log(errors?.name?.message)



    return (
        <>



            <form onSubmit={handleSubmit(cbSubmit)} className='form'>

                <label className="labelfor"> {errors?.name?.message ? <p className="paragrafor">{errors?.name?.message}</p> :' '}
                    <input className="inputfor"
                        {...register('name', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 2,
                                message: '       2 '
                            },
                        })}
                        type='text'
                        placeholder="Anonimus"
              
                    >
                    </input>
                </label>

                <label className="labelfor"> {errors?.url?.message ? <p className="paragrafor">{errors?.url?.message}</p> :' URL '}
                    <input className="inputfor"
                        {...register('url', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 12,
                                message: 'url        12 '
                            },
                            pattern: {
                                value:/https:\/\//gm,
                                message: 'url     https://'
                            }

                        })}
                        type='text'
                        placeholder="https://unsplash..."
               
                    ></input>
                </label>

                <label className="labelfor"> {errors?.head?.message ? <p className="paragrafor">{errors?.head?.message}</p> :'  '}
                    <input className="inputfor"
                        {...register('head', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 3,
                                message: '       3 '
                            },
                        })}
                        type='text'
                        placeholder=""
              
                    ></input>
                </label>

                <label className="labelfor"> {errors?.body?.message ? <p className="paragrafor">{errors?.body?.message}</p> :'  '}
                    <input className="inputfor"
                        {...register('body', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 10,
                                message: '       10 '
                            },
                        })}
                        type='text'
                        placeholder=" "
          
                    ></input>
                </label>


                <Button type="submit" variant="contained" > </Button>


            </form>





        </>
    )


}



// register -   ,       .            . handleSubmit -    ,    .      . fotmState -         .      errors.